import React from 'react';
import SearchBar from './search_bar';
import List from './list';
import { fetchAnime } from '../../actions/search/anime';

export default class SearchFeed extends React.Component {
  constructor() {
    super();
    this.state = {
      items: [],
    };
  }

  onSearch(term) {
    fetchAnime(term).then( data => this.setState({ items: data.data }));
  }

  render() {
    return (
    <div>
      <SearchBar 
          onSearchTermChange={(term) => this.onSearch(term)} />
      <List />
        <ul>
        {
          this.state.items.map(item => {
            const titleType = Object.keys(item.attributes.titles)[0];
            const title = item.attributes.titles[titleType];
            const coverImage = item.attributes.coverImage;
            const imageUrl = coverImage ? coverImage.original : "";
            return (
              <li key={title}>
                { title }
                <img src={imageUrl} height="25%" width="25%" alt=""/>
              </li>
            );
          })
        }
      </ul>
    </div>
    );
  }
}